<script>
	import { ShadowRoot } from './../../libraries/ShadowRoot.js';
import { onMount } from "svelte";

import { random_string } from "../../libraries/random_string";

import { StripeComponent } from "../../libraries/StripeComponent";
let stripe;
let elements;
let id = "stripe-component-"+random_string(6);
export let public_key = "";
let mounted = "";
let cardElement
let cardErrorElement
tripod_register.stripe = {};
tripod_register.stripe.module = {};
tripod_register.stripe.card = null;


onMount(()=>{
    StripeComponent(()=>{
        if(mounted === "mounted"){ return; }
        tripod_register.stripe.module = Stripe(public_key);
        elements = tripod_register.stripe.module.elements();
        let container = $ShadowRoot.querySelector(`#${id}`);
        cardElement = container.querySelector(".card-element");
        cardErrorElement = container.querySelector(".card-element-error");

        let style = {
            base: {
                color: '#32325d',
                fontFamily: '"Montserrat", Helvetica Neue", Helvetica, sans-serif',
            },
            invalid: {
                color: '#fa755a',
                iconColor: '#fa755a'
            }
        };
        
        let card = elements.create("card", { style: style });
        tripod_register.stripe.card= card;
        card.mount(cardElement);
        mounted = "mounted";
        tripod_register.stripe.cardErrorElement = cardErrorElement;
        card.on('change', ({error}) => {
            if (error) {
                cardErrorElement.textContent = error.message;
            } else {
                cardErrorElement.textContent = '';
            }
        });
    })

})


</script>
<style>
.StripeComponent{
    padding: 0.75rem;
    border-radius: 8px;
    border: 1px solid #AAB8C2;
}
</style>
<div class="StripeComponent" id="{id}">
    <div class="card-element {mounted}">
        <!-- Elements will create input elements here -->
    </div>
    <!-- We'll put the error messages in this element -->
    <div class="card-element-error" role="alert"></div>                    
</div>
