<script>
import { turpial } from '../../libraries/turpial.js';
import StripeCard from './../../components/Elements/StripeCard.svelte';
import SimpleCheckbox from 'tripod-styles/src/components/Checkboxes/SimpleCheckbox.svelte'
import { random_string } from '../../libraries/random_string.js';
import { StoreForm } from './StoreForm.js';
import { AppForms } from '../../libraries/AppForms.js';
import { onMount } from 'svelte';

export let changeStep;
export let Data;
export let errors;



let stripe_key;
Data.is_online = Data.is_online ?? true;
let metadata = Data;
let stripe_account = $StoreForm.global_vars.stripe_account;
let version_field = (Data.is_online === true) ? "live" : "test"

if(stripe_account === "account_v2"){
    stripe_key = $StoreForm.global_vars.stripeTokens[version_field];
}else{
    stripe_key = $StoreForm.global_vars.stripeTokens_v2[version_field];
}


let lang = Data.lang;
let Affiliate = Data.affiliate;
let trial = Data.trial ?? false;
let formLang = lang.forms.planSelection;
let selectedPrice;
let buttonSubmit;
let form = null;
let IS_ONLINE =  tripod_register.is_online ?? false;
let couponElement = null;
let terms = false;
let plans;
let default_plan = Data.default_plan ?? null;
let default_to_set = null;

metadata.show_only_card = metadata.show_only_card ?? null;

if(typeof metadata.show_only_card === "string"){
    if(metadata.show_only_card !== "year" && metadata.show_only_card !== "month"){
        metadata.show_only_card = null;
    }
}


let _host = IS_ONLINE ? "https://app.3pod.io/" : "https://beta.3pod.io/";
let csrf_url = `${_host}cs_unique/user?v=3`;
let csrf_token = null;
window.addEventListener("message", function(event){
// Check sender origin to be trusted
    try {
        if (event.origin.search("app.3pod.io") < 0){
            return;
        }
        let data = event?.data ?? {};
        csrf_token = data?.tkcs ?? null;
    } catch (error) {
        
    }
}, false);

function sendEvent(){
    try {
        let dataRegister = $StoreForm.dataForm;
        dataRegister.userFromCampaign = tripod_register.userFromCampaign;
        dataRegister.add_trial_end = tripod_register?.add_trial_end ?? false;
        dataRegister.type_event = "Widget Registration";
        
        let url;
        if(IS_ONLINE === false){
            url = "http://localhost/beta.3pod.io/public/api/v1/events/registerUser";
        }else{
            url = "https://app.3pod.io/public/api/v1/events/registerUser";
        }
        turpial.fetch({
            url: [url],
            method: "POST",
            type: "text",
            data: JSON.stringify({
                token: btoa(JSON.stringify(dataRegister)).split("").reverse().join(""),
            }),
            ready: (data)=>{
                console.log(data)
            }
        })
    } catch (error) {
        
    }
}
sendEvent();


$: {
    if(terms !== false){
        terms.parentNode.parentNode.parentNode.style.minHeight = `${terms.offsetHeight + 16}px`;
    }
    if(form !== null){
        if(typeof window.PaymentFunnelsMounted === "function"){
            window.PaymentFunnelsMounted(form);
        }
    }
}
let hasDiscountFromTag = false;
let activateCoupon = false;
if((Data?.activateCoupon ?? null) === true){
    activateCoupon = true;
}


let plan_id_selected = null;
let membership_type_selected = null;

function parsePlans(coupon){
    coupon = coupon ?? {};
    errors.hide();

    plans = $StoreForm.plansFetched.filter(( plan )=>{
        let price_month = plan.price_plan;
        let interval_year_price = plan.interval_year_price ?? plan.price_plan;
        let percent_off = 100-(interval_year_price*100)/price_month;
        percent_off = Number.parseInt(percent_off);
        let offPrice = (price_month-interval_year_price)*12;
        plan.prices = [];
        let yearChecked =  false;
        let discountPercent = 0;

        let currentPrices = {
            price_month: price_month,
            interval_year_price: interval_year_price,
        }
        let hasDiscountsPlan = false;
        if(typeof plan.discountsPlan !== "undefined"){
            discountPercent = +(plan.discountsPlan.discountPercent);
            if(discountPercent > 0){
                hasDiscountFromTag = true;
                price_month = price_month-((discountPercent/100)*price_month);
                interval_year_price = interval_year_price-((discountPercent/100)*interval_year_price);
                hasDiscountsPlan = true;
            }
        }
        if(typeof coupon.percent_off !== "undefined"){
            price_month = price_month-((coupon.percent_off/100)*price_month)
        }


        let to_push = {
            type: "month",
            is_default: false,
            percent_off: coupon?.percent_off ?? 0,
            hasDiscountsPlan: hasDiscountsPlan,
            name_plan: plan.name_plan,
            currentPrices: currentPrices,
            price_month: price_month,
            price_interval: price_month,
            real: price_month,
            yearly: price_month*12,
            globalDiscountPercent: discountPercent,
            id: "price-plan-"+random_string(6),
            checked: "checked='checked'",
        };

        if(default_plan !== null){
            if(typeof default_plan?.id !== "undefined" && default_plan?.interval !== "undefined"){
                if(default_plan.id === plan.__id__ && default_plan.interval === "month"){
                    to_push.is_default = true;
                    default_to_set = `input.is-checkradio[data-interval='month'][data-plan='${plan.__id__}']`;
                }
            }
        }

        plan.prices.push(to_push);


        

        plan_id_selected = plan.__id__;
        membership_type_selected = plan.membership_type;

        if(percent_off !== 0){
            yearChecked = true
            selectedPrice = interval_year_price; // price_interval

            if(typeof coupon.percent_off !== "undefined"){
                interval_year_price = interval_year_price-((coupon.percent_off/100)*interval_year_price)
            }

            let to_push = {
                type: "year",
                percent_off: coupon?.percent_off ?? 0,
                hasDiscountsPlan: hasDiscountsPlan,
                currentPrices: currentPrices,
                is_default: false,
                name_plan: plan.name_plan,
                price_month: interval_year_price,
                price_interval: interval_year_price,
                real: interval_year_price,
                yearly: interval_year_price*12,
                offPrice: offPrice,
                percent_off: percent_off,
                dont_pay: price_month*12,
                globalDiscountPercent: discountPercent,
                id: "price-plan-"+random_string(6),                
            }

            if(default_plan !== null){
                if(typeof default_plan?.id !== "undefined" && default_plan?.interval !== "undefined"){
                    if(default_plan.id === plan.__id__ && default_plan.interval === "year"){
                        to_push.is_default = true;
                        default_to_set = `input.is-checkradio[data-interval='year'][data-plan='${plan.__id__}']`;
                    }
                }
            }

            plan.prices.push(to_push)
        }

          
        return plan;
    })

    return plans;
}

parsePlans();
console.log(plans)
function updateDataForm(){
    // update any error
    errors.hide();

    let data = AppForms.retrieve({
        form: form,
        targets: ".formElements",
    })
    

    StoreForm.update("dataForm", data, $StoreForm.dataForm);
    data = $StoreForm.dataForm;
    return data;
}

let hasErrorForms = false;
function checkForm(data){
    let is_all_valid = true;
    data = data || {};

    // checks validation inputs.
    if(couponElement !== null){
        if(couponElement.dataset.valid === "false"){ is_all_valid = false; }
    }
    

    
    // check empty inputs
    let checks = ["first-name", "last-name", "email", "password", "phone", "city", "card_name_owner"];
    checks.map(( element )=>{
        if(typeof data[element] !== "string" && data[element] === ""){
            is_all_valid = false;
        }
    });

    
    
    return is_all_valid;
}


class generate_event_metadata
{
    constructor(plans, data)
    {
        let plan = plans.find((plan)=>{
            if(plan.__id__ === plan_id_selected){
                return plan;
            }
        })
        

        // SEND EVENT AND CONSTRUCT EVENT METADATA.
        let interval = data?.interval;
        
        let price = plan.prices.find((element)=>{
            if(element.type === interval){
                return element
            }
        })
        console.log(price)
        let final_price = interval === "year" ? (price.price_month * 12) : price.price_month;

        let event_data = {
            tag: (Data?.userFromCampaign ?? 'N/A'),
            percent_off: price?.percent_off ?? 0,
            email: data?.email ?? "",
            plan_id: plan_id_selected,
            interval: data?.interval ?? plan?.interval_selected ?? 'undefined-interval',
            name_plan: plan.name_plan,
            price_plan: final_price,
            membership: plan.membership_type,
        };

        return event_data;
    }
}
let payment_error = false;
let stripeToken = "";
function submitPayment(){
    let data = updateDataForm();
    let form_data = data;
    if(checkForm(data) === false){ 
        AppForms.statusButton(buttonSubmit, "enabled");
        errors.showError(lang.errors.uncompleted);
        return;
    }

    payment_error = false;

    AppForms.statusButton(buttonSubmit, "disabled");
    tripod_register.stripe.module.createToken(tripod_register.stripe.card).then(function(result) {
        if (result.error) {
            // Inform the user if there was an error.
            tripod_register.stripe.cardErrorElement.textContent = result.error.message;
            AppForms.statusButton(buttonSubmit, "enabled");
        } else {
            // Send the token to your server.
            // send data to server             
            data.stripeToken = result?.token?.id||"none";
            data.affiliate = Affiliate;
            data.userFromCampaign = tripod_register.userFromCampaign||"widget-registration";
            data.add_trial_end = tripod_register?.add_trial_end ?? false;

            data.timezone = Intl.DateTimeFormat().resolvedOptions().timeZone;
            if(data.timezone === null){
                data.timezone = new Date().getTimezoneOffset();
            }
            if(data.timezone === null){
                try {
                    data.timezone = "GMT"+new Date().toString().split("GMT")[1].split(" ")[0];
                } catch (error) {
                    
                }
            }

            data.request = csrf_token;

            let url;
            let path = "public/api/v1/widget_customer_subscription";
            if(IS_ONLINE === false){
                url = `${Data?.host_offline ?? "http://localhost/beta.3pod.io/"}${path}`;
            }else{
                url = `https://app.3pod.io/${path}`;
            }

            let main_data = data;
            
            turpial.fetch({
                url: [url],
                data: JSON.stringify(data),
                method: "POST",
                type: "text",
                ready: (data)=>{                    
                    data = JSON.parse(data[0]);
                    let message = data?.message ?? "";
                    if(data?.status === "error"){
                        errors.showError(message);
                        AppForms.statusButton(buttonSubmit, "enabled");
                        return;
                    }

                    let redirect = data?.response?.url ?? null; 
                    if(redirect === null){
                        errors.showError(message);
                        return;
                    }
                    if(typeof gtag !== "undefined"){
                        try {
                            gtag('event', 'btn_sign_up_widget_register', {"send_to": "G-XEF7QBK3Z5", 'tag': (Data?.userFromCampaign ?? 'N/A'), 'user': main_data?.email ?? ""})
                            gtag('event', 'btn_sign_up_widget_purchase', {"send_to": "G-XEF7QBK3Z5"})
                            


                            let event_data = new generate_event_metadata(plans, form_data);

                            try {
                                let on_register_event = window?.tripod_events?.on_register ?? null;
                                if(typeof on_register_event === "function"){
                                    on_register_event(event_data);
                                }
                            } catch (error) {
                                console.error(error)
                            }
                            
                            try {
                                let on_purchase_events = window?.tripod_events?.on_purchase ?? null;
                                if(typeof on_purchase_events === "function"){
                                    on_purchase_events(event_data);
                                }
                            } catch (error) {
                                console.error(error)
                            }
                        } catch (error) {
                            console.error(error)
                        }
                    }
                    setTimeout(() => {
                        window.location.href = redirect;
                    }, 2500);
                },
                onerror: (data)=>{
                    console.error(data);
                    payment_error = true;
                    AppForms.statusButton(buttonSubmit, "enabled");
                }
            })

        }
    });
}
function checkCoupon(couponElement, button){
    let input = couponElement;
    if(input.value === ""){
        input.classList.remove("coupon-success");
        input.classList.remove("coupon-error");
        input.dataset.valid = true;
        return;
    }
    
    if(input.value.length <= 3){
        input.classList.add("coupon-error");
        input.classList.remove("coupon-success");
        input.dataset.valid = false;
        parsePlans();
        return;
    }

    button.setAttribute("disabled", "");
    button.classList.add("is-loading");

    let url;
    if(IS_ONLINE === false){
        url = "http://localhost/beta.3pod.io/public/api/v1/getCoupon?coupon="+input.value;
    }else{
        url = "https://app.3pod.io/public/api/v1/getCoupon?coupon="+input.value;
    }


    turpial.fetch({
        url: [url],
        method: "POST",
        type: "text",
        ready: (data)=>{

            button.removeAttribute("disabled");
            button.classList.remove("is-loading");

            data = JSON.parse(data[0]);
            if(data?.status === "success"){
                input.classList.add("coupon-success");
                input.classList.remove("coupon-error");
                input.dataset.valid = true;

                plans = parsePlans(data.response)

            }else{
                input.classList.add("coupon-error");
                input.classList.remove("coupon-success");
                input.dataset.valid = false;
                plans = parsePlans()
            }
        }
    })
}

/**
 * 
 * @param {HTMLElement} box_element
 * @param {String} id_label
 */
function clicked_box(box_element, id_label){
    return;
    let boxes = [...form.querySelectorAll(".box-plan-column")];

    boxes.map(( /** @type {HTMLElement} */box )=>{
        box.classList.remove("is-active");
    })
    
    box_element.classList.add("is-active");

    let label = form.querySelector(`#${id_label}`);

    label.click();
}

let container_checkboxes = null;

onMount(()=>{
    if(default_to_set !== null){
        let node = form.querySelector(default_to_set);
        if(node !== null){
            node.click();
        }
    }
    

})

</script>
<header>
    <iframe src="{csrf_url}" title="tk" frameborder="0" class="is-sr-only"></iframe>
    <h2 class="has-text-black fw700 font-size-24 title-second-step">
        {formLang.title}
    </h2>
    <p class="fw500 font-size-16">
        {formLang.subtitle}
    </p>
    <p class="has-text-black fw700 subtitle-second-step font-size-24">
        <!--{@html formLang.subtitle}-->
        Paso 2 de 2
    </p>
</header>
<main class="pt-0 mt-0">
    <form action="" on:input|preventDefault="{ updateDataForm }" on:submit|preventDefault="{ submitPayment }" bind:this="{form}">
        <div>
            <input type="hidden" name="stripeToken" value={stripeToken}>
            <input type="hidden" name="card_name_owner" value={stripeToken}>
            <input type="hidden" class="formElements" name="id_plan" bind:value="{plan_id_selected}">
            <input type="hidden" class="formElements" name="membership_type" bind:value="{membership_type_selected}">
        </div>
        <div class="columns is-multiline is-marginless is-paddingless" bind:this={container_checkboxes}>
            {#each plans as plan}
                <div class="column">
                    <checkboxes-plans class="columns is-multiline is-centered box-plan-container is-flex-touch" data-plan="{plan.name_plan}">
                        {#each plan.prices.filter((item, key)=>{
                            item.is_available = true;
                            item.selected_show_only_card = false;
                            if(metadata.show_only_card !== null){
                                if(item.type !== metadata.show_only_card){
                                    item.is_available = false;
                                }
                                item.checked = true;
                                item.selected_show_only_card = true;
                            }

                            if(item.is_available){
                                return item;
                            }
                        }) as price_plan, key}
                                
                                <!-- svelte-ignore a11y-click-events-have-key-events -->
                                <section class="column {!price_plan.is_default ? "":"is-active"} {plans.length <= 1 ? "is-6 is-6-tablet" : "is-12 is-12-tablet"} box-plan-column"
                                    on:click|stopPropagation="{function(e){ clicked_box(e.currentTarget, price_plan.id); }}"
                                >
                                    <SimpleCheckbox has_circle={true} is_column={true} classes="pt-0 px-0">
                                        <input type="radio"
                                        checked={price_plan.checked||""} 
                                        name="interval"
                                        value="{price_plan.type}"
                                        on:change={(e)=>{ 
                                            selectedPrice = e.target.dataset.price; 
                                            membership_type_selected = plan.membership_type;
                                            plan_id_selected = plan.__id__;
                                        }}
                                        class="formElements">
                                        <svelte:fragment slot="content">
                                            <header class="header-element font-size-16 fw600 py-alt-alt-2 is-capitalized" style="padding: 10px 0;">
                                                {formLang[price_plan.type] ?? "Mensual"}
                                            </header>
                                            <main class="content-checkbox-plan padding-x-20 pt-0">
                                                {#if price_plan.offPrice > 0}
                                                    {#if !price_plan.selected_show_only_card}
                                                        <discount-element class="has-text-success">
                                                            {formLang.headband} {price_plan.percent_off}%
                                                        </discount-element>
                                                    {/if}
                                                    
                                                    {:else}
                                                        <br>
                                                {/if}
                                                <h2 class="fw700 font-size-24 has-text-black m-0">
                                                    {lang.plan_text} {price_plan.name_plan}
                                                </h2>
                                                <!-- in case of show_only_card disabled set default -->
                                                {#if !price_plan.selected_show_only_card}
                                                    <price-element class="is-flex is-flex-direction-column">
                                                        <price-monthly class="font-size-24 fw600 has-text-black">
                                                            {Data.currency[0]}{Number.parseFloat(price_plan.price_month).toFixed(2)} <span class="font-size-16 fw500 has-text-black">{Data.currency[1]}/{formLang.intervals2.month}</span>
                                                        </price-monthly>
                                                        <price-yearly class="font-size-14 fw400">
                                                            {Data.currency[0]}
                                                            {Number.parseFloat(price_plan.yearly).toFixed(2)}{Data.currency[1]}/{formLang.intervals2.year}
                                                        </price-yearly>
                                                    </price-element>
                                                {/if}


                                                 <!-- in case of show_only_card ENABLED set variant design -->
                                                {#if price_plan.selected_show_only_card}
                                                    <price-element class="is-flex is-flex-direction-column">
                                                        {#if price_plan.type === "year"}
                                                            <price-yearly class="font-size-20 fw600 has-text-black">
                                                                {Data.currency[0]}{Number.parseFloat(price_plan.yearly).toFixed(2)} <span class="font-size-16 fw500 has-text-black">{Data.currency[1]}/{formLang.intervals2.year}</span>
                                                            </price-yearly>
                                                        {/if}
                                                        {#if price_plan.type === "month"}
                                                            <price-monthly class="font-size-20 fw600 has-text-black">
                                                                {Data.currency[0]}{Number.parseFloat(price_plan.price_month).toFixed(2)} <span class="font-size-16 fw500 has-text-black">{Data.currency[1]}/{formLang.intervals2.month}</span>
                                                            </price-monthly>
                                                            <price-yearly class="font-size-14 fw400">
                                                                {Data.currency[0]}
                                                                {Number.parseFloat(price_plan.yearly).toFixed(2)}{Data.currency[1]}/{formLang.intervals2.year}
                                                            </price-yearly>
                                                        {/if}
                                                    </price-element>
                                                {/if}
                                            </main>
                                        </svelte:fragment>
                                    </SimpleCheckbox>
                                </section>           
                        {/each}
                    </checkboxes-plans>
                </div>
            {/each}
        </div>
        <div class="columns is-multiline" style="padding: 1.45rem 0">
            {#if trial === false}
                <div class="field field-input field-payment-funnels column is-12">
                    <div class="control has-text-left">
                        <label for="" class="fw600 label-text">{formLang.card_name_owner}</label>
                        <input type="text" value="{$StoreForm?.dataForm["card_name_owner"] ?? ""}" name="card_name_owner" placeholder="{formLang.card_name_owner}" class="input formElements is-fullwidth" minlength="4" required>
                    </div>
                </div>
                {#if hasDiscountFromTag === false || activateCoupon === true}
                    <div class="field field-input has-addons is-align-items-flex-end column is-12 field-payment-funnels field-coupon is-12-touch">
                        <div class="control has-text-left full-width">
                            <label for="" class="fw600 label-text">{formLang.coupon}</label>
                            <input bind:this="{couponElement}" type="text" value="{$StoreForm?.dataForm["coupon"] ?? ""}" name="coupon" placeholder="{formLang.coupon}" class="input formElements coupon is-fullwidth" minlength="4">
                        </div>
                        <div class="control">
                            <button type="button" on:click={function(e){ checkCoupon(couponElement, e.target) }} class="button is-primary is-marginless">
                                {formLang?.coupon_apply ?? "Aplicar"}
                            </button>
                        </div>
                    </div>
                {/if}
                <div class="field field-input column field-payment-funnels field-stripe-card is-12-touch">
                    <div class="control has-text-left">
                        <label for="" class="fw600 label-text">{formLang.card}</label>
                        <StripeCard public_key={stripe_key} />                    
                    </div>
                </div>
            {/if}
            <div class="field field-input field-payment-funnels column is-12">
                <div class="control">
                    <div class="field is-flex gap-17">
                        <input class="input-checkbox is-circle is-success is-small" id="agree-terms-3pod" value="true" type="checkbox" name="agree-terms" required>
                        <label for="agree-terms-3pod" class="is-pulled-left">
                            <p class="has-text-left font-size-16" bind:this="{terms}">
                                {#each (lang?.forms?.guarantee ?? []) as item}
                                    {@html item}
                                {/each}
                            </p>
                        </label>
                    </div>
                </div>
            </div>
            
            <div class="field field-payment-funnels column is-12">          
                {#if payment_error}
                    <div class="error-message-3pod">
                        <p class="has-text-danger has-text-centered">
                            Su pago ha sido rechazado o ha ocurrido un error inesperado.  
                        </p>
                    </div>
                {/if}
                <div class="buttons is-grouped is-centered">
                    <button bind:this={buttonSubmit} type="submit" class="button is-primary is-fullwidth">
                        {formLang.button}
                    </button>
                </div>
                <div class="navigation under--navigation-text">
                    <p class="is-underlined pointer under--navigation-back">
                        <!-- svelte-ignore a11y-click-events-have-key-events -->
                        <span on:click={changeStep("BasicForm")}>
                            {lang.goBack}
                        </span>
                    </p>
                </div>
            </div>
        </div>
    </form>
</main>
<style>
checkboxes-plans :global(checkradio-container header){
    background: transparent;
    border-bottom: 1px solid var(--color-extralightGrey);
    color: var(--color-primary);
    border-radius: 20px 20px 0 0;
}
checkboxes-plans :global(checkbox-radio), checkboxes-plans :global(checkradio-container){
    height: 100%;
}
checkboxes-plans :global(input[type='radio']:checked+checkradio-container header){
    border-bottom: 1px solid;
    color: var(--color-primary);
}
checkboxes-plans :global(checkbox-content){
    height: 100%;
    place-content: flex-end;
}



main{
    margin-top: 1.25rem;
    padding-top: 1.25rem;
}
.coupon:global(.coupon-success), .coupon:global(.coupon-success:focus){
    border: 1px solid var(--color-green);
}
