<script>
import ContentOldVersion from './partials/ContentOldVersion.svelte';
import { PlansStore } from './Plans.store.js';
import { UpdateSubscriptions } from './SubscriptionsStore';
import Subscriptions from './Subscriptions.svelte';
import { sortItems } from 'tripod-styles/src/libraries/sortItems';
import { turpial } from '../libraries/turpial.js';
import { Connect3Pod } from '../libraries/Connect3Pod.js';
import HeadersTabs from 'tripod-styles/src/components/Others/HeaderTabs/HeadersTabs.svelte'
import {HeaderTabController} from 'tripod-styles/src/components/Others/HeaderTabs/HeaderTabController'
import StaticLink from 'tripod-styles/src/components/Buttons/StaticLink.svelte'
import InputSwitch from 'tripod-styles/src/components/Inputs/InputSwitch.svelte';
import ContentNewVersion from './partials/ContentNewVersion.svelte';
import RestartableController from 'tripod-styles/src/components/Elements/RestartableController.svelte';
import restart_component from 'tripod-styles/src/components/Elements/restart_component';
import {Contents} from 'tripod-styles/src/components/ContentsPlatform.store'

export let user = {};
export let affiliate = {};
export let Data = {};
export let onSelectPlan = function(){}; // for internal modules

let lang = tripod_plans.lang ?? {};

let userFromCampaign = Data.userFromCampaign ?? null;
if(typeof user.userFromCampaign !== "undefined" || userFromCampaign === null){
    userFromCampaign = user?.userFromCampaign ?? user?.user_from_campaign ?? null;
}

let loaded_language = false;
window.lang_selected = lang.lang_selected ?? "es";
let lang_selected = window.lang_selected;
if((window?.ContentsAll) !== null){
    loaded_language = true;
}

if(!loaded_language){
    Contents.load_both(lang_selected, ()=>{
        window.ContentsAll = $Contents;
        loaded_language = true;
    });
}


let plans_rows; // restartable component
let plans_module_version = window.plans_module_version ?? "new";
let memberships = [];
let membership_names = [];
let membership_types = [];
let membership_selected = tripod_plans.selected ?? "designs";
let showOnly = tripod_plans.showOnly ?? false;
let customCardPlan = tripod_plans.customCardPlan ?? "minimal";
let title = tripod_plans.title ?? false;
let go_back = tripod_plans.go_back ?? false;
/**
 * plan selected in a external enviroment
 * will not work in internal enviroment.
 */
let plan_id_selected = tripod_plans.plan_id_selected ?? null;
let on_go_back = tripod_plans.on_go_back ?? function(){};
let subtitle = tripod_plans.subtitle ?? false;
let button_header = tripod_plans.button_header ?? false;
let button_header_on_click = tripod_plans.button_header_on_click ?? false;
let custom_title = tripod_plans.custom_title ?? false;
let subscriptions = tripod_plans.subscriptions ?? [];


let plans = tripod_plans.plans ?? false;
let all_plans = false;
let all_addons = false;
let stripe_subs = tripod_plans.stripe_subs ?? false;
let showPlans = tripod_plans.showPlans ?? true;
let showProducts = tripod_plans.showProducts ?? true;
let links = tripod_plans.links ?? true;
let plansSelector = tripod_plans?.links?.plansSelector ?? false;
let host = tripod_plans.host;
let section = null;
let fetched = false;
let interval = "month";
let has_chooser_plan_module = false;
if(typeof tripod_register !== "undefined"){
    has_chooser_plan_module = tripod_register?.has_chooser_plan_module ?? false;
}
// set membership_selected in plans store.
PlansStore.update("membership_selected", membership_selected);

if(custom_title === false){
    window.get_contents_allt
    title = window.get_contents_all("ctn_PVsSRO") ?? "Membresas";
    subtitle = window.get_contents_all("ctn_8XG-IW") ?? "Tenemos la membresa justo a la medida de tu negocio";
}

// updater plans for external enviroment
window.tripod_plans.update_plans_state = ()=>{
    PlansStore.reset();
};

$: {
    if(section !== null){
        turpial.linkCSS({
            target: section,
            url: [
                "https://cdn.jsdelivr.net/gh/Yerikmiller/3pod-us.github.io@latest/icons/platform/tripod-pack/tripod-pack.css",
                "https://cdn.jsdelivr.net/gh/Yerikmiller/3pod-us.github.io@latest/icons/iconsax/vuesax-outline/IconFont.css",
                "https://cdnjs.cloudflare.com/ajax/libs/animate.css/4.1.1/animate.min.css"
            ], ready: ()=>{}
        })
    }
}


window.tripod_plans.lang.on_plan_type = window.tripod_plans.lang?.on_plan_type ?? {
    PLAN: "Membresa",
    PRODUCT: "Pago nico",
}

let available_custom_cards = [
    "minimal",
    "default"
];

let find_custom_card = available_custom_cards.find( (r)=>  r === customCardPlan);
if(typeof find_custom_card === "undefined" || typeof customCardPlan !== "string"){
    customCardPlan = "minimal"
}


tripod_plans.updateSubscriptions = (r)=> UpdateSubscriptions(r);

UpdateSubscriptions();

if(plansSelector !== false){

    if(typeof plansSelector !== "object"){
        plansSelector = false;
    }
}
let membership_icons_fetched = {
    funnels: "icon-tripod-video-tick",
    campaigns: "icon-tripod-speaker",
    video_hosting: "icon-tripod-folder-video",
    ia_content: "icon-tripod-v-magicpen",
    designs: "icon-tripod-design-tools",
};


let showCurrentMemberships = tripod_plans.showCurrentMemberships ?? false;

if(showOnly !== false){
    tripod_plans.selected = membership_selected = showOnly;
}

let referralsDiscounts = [];
let ordered_memberships = [];
function getReferredBy(public_key)
{
    public_key = user?.public_key ?? "";
    let referred_by = user?.referred_by ?? user?.referrer_id ?? "";
    Connect3Pod(`${host}public/api/v1/affiliates/referredByData`, {
        data: {
            public_key_user: public_key.split("").reverse().join(""),
            public_key_affiliate: referred_by.split("").reverse().join(""),
        },
        ready: (data)=>{
            try {
                data = JSON.parse(data);
                referralsDiscounts = data.referralsDiscounts;
            } catch (error) {
                referralsDiscounts = {};
            }
        }
    })
}

function onReadygetPlans(data, addons){
    /**
     * Si quieres cambiar el orden de los botones y sus ttulos
     * debes cambiar el orden de $memberships, $membership_names & $membership_types
    */

    getReferredBy(user.referred_by);
    
    let showOnly = tripod_plans.showOnly ?? false;
    let order = tripod_plans.order ?? false;
    let fields = tripod_plans.lang.fields ?? false;
    if(order === false){
        console.error("You need to supply a order field. ex: [...memberships]")
        return;
    }
    
    if(showOnly !== false){
        data = {
            [showOnly]: data[showOnly],
        }
        
    }
    memberships = data;
    tripod_plans.plans = memberships;
    all_addons = addons;
    tripod_plans.addons = addons;
    Object.values(memberships).map(( plans, key )=>{
        let name = Object.keys(memberships)[key];        
        plans.map(( plan, position )=>{
            if((plan.discountsPlan ?? null) !== null){
                plan.discountPercent = plan.discountsPlan.discountPercent ?? null;
                if(plan.discountPercent !== null){
                    plan.discountPercent = +(plan.discountPercent)
                }
            }

            memberships[name][position].price_plan = +(plan.price_plan);
            memberships[name][position].position_plan = +(plan.position_plan);
            memberships[name][position].price_on_year_monthly = +(plan.price_plan)*12;
            
            if(plan.interval_year_price ?? "" !== ""){
                memberships[name][position].price_on_year_yearly = +(plan.interval_year_price)*12;
            }
            
            if(plan.type === "PRODUCT"){
                memberships[name][position].price_on_year_monthly = +(plan.price_plan);
                memberships[name][position].price_on_year_yearly = +(plan.price_plan);
            }

            memberships[name][position].is_recommended = memberships[name][position]?.is_recommended ?? "Default";
            
            memberships[name] = sortItems(memberships[name], "price_on_year_monthly", "asc");
        })
    })
    
    let membershipsOrdered = {};
    
    /**
     * Para ordenar los datos con el campo order.
     * para mostrar solo uno de los elementos
    */
    order.map(( item )=>{
        if(showOnly !== false){
            if(item === showOnly){
                membershipsOrdered[item] = memberships[item]
                return;
            }
        }else{
            membershipsOrdered[item] = memberships[item]
        }
    })    
    memberships = membershipsOrdered;
    /**
     * ordered by importance
    */
    membership_names = fields;
    /**
     * ordered by importance
    */
    membership_types = order;
    console.log(membership_selected)

    plans = tripod_plans.plans;
    all_plans = Object.values(plans).filter(( subs )=>{
        let get = [];
        subs.map((plan)=>{
            get.push(plan)
        })
        return get;
    }).flat();
    PlansStore.update("plans_views", memberships[membership_selected]);
    return plans;
}



function changeInterval(interval_checked){
    interval = interval_checked;
}

$: {
    if(section !== null && fetched === false){
        let urlGetPlans = host+"api/getPlans";
        let urlGetAddons = host+"api/getAddons";
        
        if(userFromCampaign !== null){
            urlGetPlans = host+"api/getPlans?tag="+btoa(userFromCampaign);
        }

        turpial.fetch({
            url: [urlGetPlans, urlGetAddons],
            method: "POST",
            type: "text",
            ready: function(data){
                let plans = JSON.parse(data[0] ?? "{}");
                let addons = JSON.parse(data[1] ?? "{}");

                window.getPlans3Pod = function(){
                    return plans?.response||[];
                }
                window.getAddons3Pod = function(){
                    return addons?.response||[];
                }

                if(plans.status === "error"){
                    console.error("An error has been occurred loading API getPlans from 3pod.");
                }
                if(plans.status === "success"){
                    console.log(plans.response)
                    onReadygetPlans(plans.response, (addons?.response||{}));
                    fetched = true;
                }
            },
            onerror: function(data){
                console.warn(data);
                memberships = [];
            }
        })
    }
}


let boxes_container;
function changeMembership(membership_selected){

    try {
        tripod_plans.onChangeMembership(memberships[membership_selected])
    } catch (error) {
        
    }

    PlansStore.update("plans_views", memberships[membership_selected]);
    PlansStore.update("membership_selected", membership_selected);
    membership_selected = membership_selected;
} 


let header_tabs = new HeaderTabController();
header_tabs.on_change = (item)=>{
    item = item ?? null;
    PlansStore.update("current_item_tab", item);
    plans_rows = restart_component();
}

$:console.log(`Membresa: ${membership_selected}`)

$: {
    if(membership_types.length > 0){
        let items = Object.values(memberships);
        let selected_items = [];
        items.map((plans)=>{
            let membership_type = plans[0].membership_type;

            selected_items.push({
                icon: membership_icons_fetched[membership_type],
                title: membership_names[membership_type],
                current: membership_type === membership_selected,
                membership: membership_type,
                on_click: ()=>{
                    if(plansSelector === false){
                        changeMembership(membership_type);
                    }else{
                        window.location.href = plansSelector[plans[0].membership_type];
                        return 'preventDefault'
                    }
                }
            })
        });

        header_tabs.set_items(selected_items);
    }
}

/*
[
    {
        icon: "";
        title: "";
        current: false;
        on_click: ()=>{};
    }
]
*/

</script>
{#if loaded_language}
    <section bind:this={section} class="box main-section-plans">
        {#if section !== null}
        <header>
            {#if go_back}
                <StaticLink on:click={on_go_back}>
                    <span class="has-text-primary button-go-back">
                        {go_back}
                    </span>
                </StaticLink>
            {/if}
            {#if title !== false}
                <div class="is-flex is-justify-content-space-between is-relative">
                    <header-titles class="is-block">
                        <h2 class="has-text-black font-size-28 font-size- fw700 has-text-left">
                            {title}
                        </h2>
                        <p class="has-text-black has-text-left">
                            {subtitle}
                        </p>
                    </header-titles>
                    {#if button_header}
                        <button class="button is-primary" on:click={button_header_on_click}>
                            {button_header}
                        </button>
                    {/if}
                </div>
            {/if}
            {#if has_chooser_plan_module}
                <h2 class="has-text-black font-size-24 fw700 has-text-centered">
                    {Contents.set_content(window.get_contents_all("ctn_11Tj") ?? "Paso {{ stetp }} de {{ current }}", {
                        stetp: "2",
                        current: "3"
                    })}
                </h2>
            {/if}    
        </header>
        <main>
            {#if showCurrentMemberships === true}
                <Subscriptions subscriptions={subscriptions} tripod_plans={window.tripod_plans} lang={lang} all_addons={all_addons} all_plans={all_plans} stripe_subs={stripe_subs} />
            {/if}
            {#if showPlans}
                {#if showOnly === false}
                    {#if header_tabs.items.length > 1}
                        <!-- buttons navigation between memberships -->
                        <HeadersTabs controller={header_tabs} icon_classes="font-size-24 font-size-14-touch" tabs_classes={'fit-content-width'} />
                    {/if}
                    
                {/if}
                <RestartableController bind:data={plans_rows}>
                    {#if plans_module_version !== "new"}
                        <ContentOldVersion
                            changeInterval={changeInterval}
                            lang={lang}
                            membership_selected={membership_selected}
                            onSelectPlan={onSelectPlan}
                            affiliate={affiliate}
                            user={user}
                            interval={interval}
                            customCardPlan={customCardPlan}
                            tripod_plans={window.tripod_plans}
                        />
                    {/if}
                    {#if plans_module_version === "new"}
                        <ContentNewVersion
                            changeInterval={changeInterval}
                            lang={lang}
                            membership_selected={membership_selected}
                            onSelectPlan={onSelectPlan}
                            affiliate={affiliate}
                            user={user}
                            interval={interval}
                            customCardPlan={customCardPlan}
                            tripod_plans={window.tripod_plans}
                        />
                    {/if}
                </RestartableController>
            {/if}
        </main>
        {/if}
    </section>
{/if}
<style>
.main-section-plans{
    padding: 2.75rem;
}
section{
    padding-bottom: 2rem;
}
main{
    margin-top: 1rem;
}
@media only screen and (max-width:900px){
    header {
        padding: 0 1.25rem;
    }
}
</style>